@import "sass/index";

.root {
  height: 0;

  position: absolute;
  top: 0;
  z-index: 1;
  width: 100%;
  pointer-events: none;

  @media (max-width: $mobileScreen) {
    padding: 0 22px;
  }
}

.img {
  position: absolute;
  top: 0;
  right: 22px;
  width: 32%;
  transform-origin: bottom right;
  transform: translateY(-33%);

  @media (max-width: $mobileScreen) {
    margin-right: 22px;
  }
}
